import React from 'react';
import SpecalizeCard from './SpecalizeCard'; // Make sure to adjust the path
import data from '../data.js'; // Correct the path to your data file

export default function TechnologyComponent() {
  return (
    <div className='technology-container'>
      <h1 id="technology-header">What Services We offer to our Clients</h1>
      <div className='card-container'>
        {data.map(eachSpecalized =>
          <SpecalizeCard
                    title = {eachSpecalized.title}
                    imageUrl = {eachSpecalized.image_url}
                    description = {eachSpecalized.description}
            />
        )}
      </div>
    </div>
  );
}
