import React from 'react';

import about from '../assest/images/choose us.png'; // Update the image file extension if needed


export default function MyComponent() {
  return (
    <div className="container-of-aboutUs">
      <h1>Why Choose us?</h1>
      <br/><br/>
      <div className='container-inner-aboutus'>
        <div className='left-aboutus-container'>
          <p>We are a newly established web design agency with some industry experience in web development. From designing high-quality websites to launching them on the internet, we prioritize creativity and always put our clients first.</p>
        </div>

        <div className='right-aboutus-container'>
          <img className = "aboutus-img" src={about} alt="Image of company office" />
        </div>
      </div>
    </div>
  );
}
