import React from 'react'

export default function Faq() {
  return (
    <div className='faq-container'>
        <h1>FAQ</h1>
        <div class="accordion accordion-flush" id="accordionFlushExample">
        <div class="accordion-item">
            <h2 class="accordion-header" id="flush-headingOne">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseOne" aria-expanded="false" aria-controls="flush-collapseOne">
                What prerequisites are necessary before visiting us?
            </button>
            </h2>
            <div id="flush-collapseOne" class="accordion-collapse collapse" aria-labelledby="flush-headingOne" data-bs-parent="#accordionFlushExample">
            <div class="accordion-body">Each client should have a registered domain name and web hosting. If not, we can arrange a call to assist you in setting up your account and registering your domain name</div>
            </div>
        </div>
        <div class="accordion-item">
            <h2 class="accordion-header" id="flush-headingTwo">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseTwo" aria-expanded="false" aria-controls="flush-collapseTwo">
                How will you work on my website once I have registered for a domain name and hosting provider?
            </button>
            </h2>
            <div id="flush-collapseTwo" class="accordion-collapse collapse" aria-labelledby="flush-headingTwo" data-bs-parent="#accordionFlushExample">
            <div class="accordion-body">Once our client has set up their hosting account and domain name, we proceed by collecting their email and password for the client account. Subsequently, we install WordPress on their account and commence development. This allows the client to view their site during the construction process, minimizing the risk of migration errors</div>
            </div>
        </div>
        <div class="accordion-item">
            <h2 class="accordion-header" id="flush-headingThree">
            <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseThree" aria-expanded="false" aria-controls="flush-collapseThree">
                What hosting provider do you prefer us to register?
            </button>
            </h2>
            <div id="flush-collapseThree" class="accordion-collapse collapse" aria-labelledby="flush-headingThree" data-bs-parent="#accordionFlushExample">
            <div class="accordion-body">We recommend that our clients choose Hostinger for domain registration and WordPress installation. Hostinger offers competitive pricing, ranging from affordable to premium hosting options.</div>
            </div>
        </div>
        </div>
</div>
  )
}
