import React from 'react';
import { Flex, Box } from '@radix-ui/react-primitive';
import Card from 'react-bootstrap/Card';
export default function SpecalizeCard(props) {
  return (
    <div>
<Card >
      <Card.Img variant="top" src={props.imageUrl} />
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <br />
        <Card.Text>
        {props.description}
        </Card.Text>
        
      </Card.Body>
    </Card>
     
    </div>
  );
}
