import React from 'react'
import dataPackage from '../packageData.js';
import PackageCard from './PackageCard.jsx';
export default function PackageOffer() {
  return (
    <div className='technology-container'>
      <h1 id="technology-header">Our Development Packages </h1>
      <div className='card-container'>
      {dataPackage.map(eachPackage =>
        <PackageCard
            title = {eachPackage.title}
            pages = {eachPackage.offer.numberofPages}
            seo = {eachPackage.offer.seo}
            webDesign = {eachPackage.offer.webDesign}
            customization = {eachPackage.offer.customization}
            price = {eachPackage.offer.price}
        />
        )}
      </div>
    </div>
  )
}
