import React from 'react';
import { Flex, Box } from '@radix-ui/react-primitive';
import Card from 'react-bootstrap/Card';

export default function PackageCard(props) {
  return (
    <div>
      <Card >
      <Card.Img variant="top" src={props.imageUrl} />
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <br />
        <Card.Text>
            <h5>{props.pages}</h5>
            <h5>{props.seo}</h5>
            <h5>{props.webDesign}</h5>
            <h5>{props.customization}</h5>
        </Card.Text>
        <h2>{props.price}</h2>
      </Card.Body>
    </Card>
      {/* <div className="card">
        <div className="card-body">
          <h1 className="card-title">{props.title}</h1>
          <br />
          <h5>{props.pages}</h5>
            <h5>{props.seo}</h5>
            <h5>{props.webDesign}</h5>
            <h5>{props.customization}</h5>
            <br />
            <h2><span id = "span">{props.price}</span></h2>
        </div>
      </div> */}
    </div>
  );
}
